document.addEventListener('DOMContentLoaded', function(){
    eventListeners();
    darkMode();
    alertaFormulario();
    descripcion();
    verMas();
});

//Menu hamburgesa
function eventListeners() {
    const menu = document.querySelector(".menu-mobile");
    menu.addEventListener('click', menuResponsive);

    // Muestra campos condicionales formulario
    const metodoContacto = document.querySelectorAll('input[name="contacto[contacto]"]');
    metodoContacto.forEach(input => addEventListener('click', mostrarMetodoContacto));
}

// Telefono o Email FORMULARIO
function mostrarMetodoContacto(e){
    const contactoDiv = document.querySelector('#contacto');

    if (e.target.value === "telefono") {
        contactoDiv.innerHTML = 
        '<p>Ingrese su telefono:</p>' +
        '<input id="telefono" type="tel" placeholder="Tu TelÃ©fono" name="contacto[telefono]" required>' +
        '<p> Elija la fecha y la hora en la que desea ser contactado</p>' + 
        '<label for="fecha">Fecha:</label>' +
        '<input type="date" id="fecha" name="contacto[fecha]" required>' +
        '<label for="hora">Hora:</label>' +
        '<input type="time" id="hora" min="08:00" max="18:00" name="contacto[hora]" required>';
    } else if (e.target.value === "email") {
        contactoDiv.innerHTML =
        '<p>Ingrese su email:</p>' +
        '<input id="mail" type="email" placeholder="Tu Email" name="contacto[email]" required>';
    } 

}

function menuResponsive(){
    const nav = document.querySelector(".navegacion-ul");
    nav.classList.toggle("mostrar");
}

function descripcion(){
    const descripciones = document.querySelectorAll(".descripcion");
    const limite = 100;

    descripciones.forEach(function(descripcion) {
        const textoCompleto = descripcion.textContent;
        let textoLimitado = textoCompleto.slice(0, limite);

        if(textoCompleto.length > limite) {
            textoLimitado += '...';
        }
        descripcion.textContent = textoLimitado;
    });
}

